import NProgress from 'nprogress'
import 'nprogress/nprogress.css'
import router, {fleetSteps, individualSteps, customFeeSteps} from './router'
import store from './store'
import {getToken} from './utils/auth'

router.beforeEach((to, from, next) => {
  NProgress.start()
  if (getToken()) {
    if (to.path === '/') {
      next({path: '/admin'})
    } else {
      if (store.getters.menus === undefined || store.getters.menus.length === 0) {
        store.dispatch('GetInfo').then(response => {
          next({...to}) // hack
        }).catch(() => {
          store.dispatch('LogOut').then(() => {
            next({path: '/'})
          })
        })
      } else {
        // 校验路由访问权限
        const meta = to.meta
        if (meta.authority !== undefined && !store.getters.permissions.includes(meta.authority)) {
          next({path: '/404'})
        }
        // 个车编制统一处理
        if (to.path.startsWith('/admin/individual/declare')) {
          const index = individualSteps.findIndex(item => to.path.startsWith(item))
          let step = index + 2
          if (index >= 0) {
            store.commit('HIDE_LIFE_OPTION')
            store.commit('HIDE_VIEW_PARAMETERS_OPTION')
          } else if (to.path === '/admin/individual/declare/paramConfig') {
            store.commit('SHOW_LIFE_OPTION')
            store.commit('SHOW_VIEW_PARAMETERS_OPTION')
            step = 0
          } else if (to.path === '/admin/individual/declare/recommend') {
            store.commit('HIDE_LIFE_OPTION')
            store.commit('HIDE_VIEW_PARAMETERS_OPTION')
            step = 1
          } else if (to.path === '/admin/individual/declare') {
            store.commit('SHOW_LIFE_OPTION')
            store.commit('SHOW_VIEW_PARAMETERS_OPTION')
            step = 0
          }
          // 个车统一更新进度条
          store.commit('SET_STEP_PROCESS', step)
        } else if (to.path.startsWith('/admin/fleet/declare/modify')) {
          // 车队编制更新进度条
          const fleetIndex = fleetSteps.findIndex(item => to.path.startsWith(item))
          store.commit('SET_FLEET_STEP_PROCESS', fleetIndex + 1)
        } else if (to.path.startsWith('/admin/agency/config/customDisFee')) {
          // 代理协议自定义差异配费更新进度条
          const customFeeIndex = customFeeSteps.findIndex(item => to.path.startsWith(item))
          store.commit('SET_CUSTOM_DIS_FEE_STEP_PROCESS', customFeeIndex + 1)
        }
        next()
      }
    }
  } else if (to.path === '/') {
    next()
  } else {
    next({path: '/'})
  }
})

router.afterEach((to, from) => {
  NProgress.done()
})



