// 个车需要隐藏表单操作按钮的路由地址
export const individualSteps = [
  '/admin/individual/declare/custom',
  '/admin/individual/declare/confirm',
  '/admin/individual/declare/config',
  '/admin/individual/declare/supply'
]

// 车队修改地址
export const fleetSteps = [
  '/admin/fleet/declare/modify/riskInfo',
  '/admin/fleet/declare/modify/configProtocol',
  '/admin/fleet/declare/modify/collect'
]

// 代理协议自定义差异配费地址
export const customFeeSteps = [
  '/admin/agency/config/customDisFee/two',
  '/admin/agency/config/customDisFee/three'
]



