import {
  SET_FLEET_STEP_PROCESS
} from '../mutation-types'

const fleet = {
  state: {
    stepProcess: 0
  },
  mutations: {
    [SET_FLEET_STEP_PROCESS]: (state, step) => {
      state.stepProcess = step
    }
  },
  actions: {}
}

export default fleet



