import {initForm, initPersonForm, initRealForm} from 'api/inspect'
import {
  SET_INSPECT_FORM_DATA,
  SET_PERSON_FORM_DATA,
  SET_REAL_FORM_DATA
} from '../mutation-types'
const inspect = {
  state: {
    formData: {
      orgList: [],
      channelList: [],
      typeList: [],
      versionList: []
    },
    personFormData: {
      orgList: [],
      channelList: [],
      feeModelList: [],
      monthList: [],
      timeRangeList: []
    },
    realFormData: {
      orgList: [],
      channelList: [],
      sizeList: [],
      timeRangeList: [],
      productTypeList: []
    }
  },
  mutations: {
    [SET_INSPECT_FORM_DATA]: (state, data) => {
      state.formData = data
    },
    [SET_PERSON_FORM_DATA]: (state, data) => {
      state.personFormData = data
    },
    [SET_REAL_FORM_DATA]: (state, data) => {
      state.realFormData = data
    }
  },
  actions: {
    initFormData ({commit}) {
      return new Promise((resolve, reject) => {
        initForm().then(response => {
          commit('SET_INSPECT_FORM_DATA', response.data)
          resolve()
        }).catch(error => {
          reject(error)
        })
      })
    },
    initPersonFormData ({commit}) {
      return new Promise((resolve, reject) => {
        initPersonForm().then(response => {
          commit('SET_PERSON_FORM_DATA', response.data)
          resolve()
        }).catch(error => {
          reject(error)
        })
      })
    },
    initRealFormData ({commit}) {
      return new Promise((resolve, reject) => {
        initRealForm().then(response => {
          commit('SET_REAL_FORM_DATA', response.data)
          resolve()
        }).catch(error => {
          reject(error)
        })
      })
    }

  }

}
export default inspect



