import request from 'utils/request'

export const login = user => {
  return request({
    url: '/api/login',
    method: 'post',
    data: user
  })
}

export const refreshToken = token => request({
  url: '/api/refreshToken',
  method: 'get',
  params: { token }
})

export const getInfo = token => {
  return request({
    url: '/api/getInfo',
    method: 'get',
    param: token
  })
}



