import {initConfigForm} from 'api/agency'
import {
  SET_CONFIG_FORM_DATA,
  SET_CUSTOM_DIS_FEE_STEP_PROCESS
} from '../mutation-types'

const agency = {
  state: {
    stepProcess: 0,
    configFormData: {
      channelList: [],
      sourceList: []
    }
  },
  mutations: {
    [SET_CONFIG_FORM_DATA]: (state, data) => {
      state.configFormData = data
    },
    [SET_CUSTOM_DIS_FEE_STEP_PROCESS]: (state, step) => {
      state.stepProcess = step
    }
  },
  actions: {
    initConfigFormData ({commit}) {
      return new Promise((resolve, reject) => {
        initConfigForm().then(response => {
          commit('SET_CONFIG_FORM_DATA', response.data)
          resolve()
        }).catch(error => {
          reject(error)
        })
      })
    }
  }

}
export default agency



