import {initForm} from 'api/budget'
import {
  SET_BUDGET_FORM_DATA,
  SET_ADD_DATA
} from '../mutation-types'

const budget = {
  state: {
    formData: {
      companyList: [],
      channelList: [],
      types: [],
      boreList: [],
      productTypeList: []
    },
    addData: []
  },
  mutations: {
    [SET_BUDGET_FORM_DATA]: (state, data) => {
      state.formData = data
    },
    [SET_ADD_DATA]: (state, data) => {
      state.addData = data
    }
  },
  actions: {
    initFormData ({commit}) {
      return new Promise((resolve, reject) => {
        initForm().then(response => {
          commit('SET_BUDGET_FORM_DATA', response.data)
          resolve()
        }).catch(error => {
          reject(error)
        })
      })
    },
    setAddData ({commit}, data) {
      // TODO 此处需要根据data中的预算制定机构查询下属的全部二级机构
      const subCompanys = ['北京分公司', '上海分公司', '江苏省分公司', '山东省分公司', '山西省分公司']
      const meta = {
        subInstitution: '支公司',
        type: data.type,
        channel: data.channel,
        timeSlot: data.timeSlot,
        bore: data.bore,
        productType: data.productType,
        targetPremium: '',
        targetOperaCostRate: '',
        targetReimburseRate: '',
        targetMarketCostRate: '',
        targetNonMarketCostRate: ''
      }
      const addData = []
      subCompanys.forEach(item => {
        const dataItem = {
          ...meta
        }
        dataItem.subInstitution = item
        addData.push(dataItem)
      })
      commit('SET_ADD_DATA', addData)
    }
  }
}

export default budget



