import request from 'utils/request'

export const initForm = () => {
  return request({
    url: '/api/inspect/initData',
    method: 'get'
  })
}

export const initPersonForm = () => {
  return request({
    url: '/api/inspect/initPersonData',
    method: 'get'
  })
}

export const initRealForm = () => {
  return request({
    url: '/api/inspect/initRealFormData',
    method: 'get'
  })
}



