import {
  SET_STEP_PROCESS,
  SHOW_LIFE_OPTION,
  HIDE_LIFE_OPTION,
  SHOW_VIEW_PARAMETERS_OPTION,
  HIDE_VIEW_PARAMETERS_OPTION,
  SECTOR_PARAM,
  SHOW_ROLLBACK,
  HIDE_ROLLBACK,
  SHOW_VERSION,
  HIDE_VERSION,
  TOGGLE_LIFE_FORM,
  SET_VERSION,
  SET_SECTORS
} from '../mutation-types'
import {querySector} from '@/api/individual'

const individual = {
  state: {
    stepProcess: 0,
    lifeFormOptionShow: true,
    viewParameters: true,
    rollbackFlag: false,
    versionFlag: false,
    param: {
      subCompanyCode: '',
      channel: '',
      rollback: '',
      version: ''
    },
    sectors: [],
    sectorParam: [],
    paramTableShow: false
  },
  mutations: {
    [SET_STEP_PROCESS]: (state, step) => {
      state.stepProcess = step
    },
    [SHOW_LIFE_OPTION]: (state) => {
      state.lifeFormOptionShow = true
    },
    [HIDE_LIFE_OPTION]: (state) => {
      state.lifeFormOptionShow = false
    },
    [SHOW_VIEW_PARAMETERS_OPTION]: (state) => {
      state.viewParameters = true
    },
    [HIDE_VIEW_PARAMETERS_OPTION]: (state) => {
      state.viewParameters = false
    },
    [SHOW_ROLLBACK]: (state) => {
      state.rollbackFlag = true
    },
    [HIDE_ROLLBACK]: (state) => {
      state.rollbackFlag = false
    },
    [SHOW_VERSION]: (state) => {
      state.versionFlag = true
    },
    [HIDE_VERSION]: (state) => {
      state.versionFlag = false
    },
    [TOGGLE_LIFE_FORM]: (state) => {
    },
    [SET_VERSION]: (state, data) => {
      state.param.version = data
    },
    [SET_SECTORS]: (state, data) => {
      state.sectors = data
    },
    [SECTOR_PARAM]: (state, data) => {
      state.sectorParam = data
      state.paramTableShow = true
    }
  },
  actions: {
    queryParam ({commit, state}) {
      querySector(state.param).then(response => {
        commit('SECTOR_PARAM', response.data)
      })
    }
  }
}

export default individual



