import request from 'utils/request'

// 个车初始化查询表单数据
export const initData = () => {
  return request({
    url: '/api/initData',
    method: 'get'
  })
}

export const sectorList = () => {
  return request({
    url: '/api/sectorlist',
    method: 'get'
  })
}

// 根据条件查询对应板块信息
export const querySector = (param) => {
  return request({
    url: '/api/querySector',
    method: 'get',
    param
  })
}

// 智能推荐
export const recommend = (param) => {
  return request({
    url: '/api/recommend',
    method: 'get',
    param
  })
}

// 根据板块获取维度信息
export const getDimensionBySector = (param) => {
  return request({
    url: '/api/getDimensionBySector',
    method: 'get',
    param
  })
}

// 自定义维度切分获取板块维度信息
export const customDimension = (param) => {
  return request({
    url: '/api/customDimension',
    method: 'get',
    param
  })
}

export const querySectorInfo = (id) => {
  return request({
    url: '',
    method: 'get',
    params: {id}
  })
}

// 生命表列表获取信息
export const lifeTable = (param) => {
  return request({
    url: '/api/lifeTable',
    method: 'get',
    param
  })
}



