import {TOGGLE_MENU_COLLAPSE} from '../mutation-types'

const app = {
  state: {
    menuCollapse: false,
    tableHeader: {
      background: 'transparent',
      color: 'white'
    }
  },
  mutations: {
    [TOGGLE_MENU_COLLAPSE]: state => {
      state.menuCollapse = !state.menuCollapse
    }
  }
}

export default app



