import {login, getInfo} from 'api/system'
import {
  getToken,
  setToken,
  removeToken
} from 'utils/auth'
import {
  SET_TOKEN,
  SET_MENUS,
  SET_ELEMENTS,
  SET_PERMISSIONS
} from '../mutation-types'

const user = {
  state: {
    token: getToken(),
    // 菜单
    menus: [],
    // 操作权限
    elements: [],
    // 用户路由权限 在路由前置守卫验证访问权限时用到
    permissions: []
  },
  mutations: {
    [SET_TOKEN]: (state, data) => {
      state.token = data
    },
    [SET_MENUS]: (state, data) => {
      state.menus = data
    },
    [SET_ELEMENTS]: (state, data) => {
      state.elements = data
    },
    [SET_PERMISSIONS]: (state, data) => {
      state.permissions = data
    }
  },
  actions: {
    Login ({commit, state}, user) {
      removeToken()
      commit('SET_MENUS', [])
      commit('SET_ELEMENTS', [])
      return new Promise((resolve, reject) => {
        login(user).then(response => {
          const data = response.data
          if (data.result) {
            setToken(data.token)
          }
          resolve(data)
        }).catch(error => {
          console.log('login error')
          reject(error)
        })
      })
    },
    GetInfo ({commit}) {
      return new Promise((resolve, reject) => {
        getInfo(getToken()).then(response => {
          const data = response.data
          const menus = data.menus
          const elements = {}
          let permissions = []
          data.elements.forEach(element => {
            elements[element.code] = true
            permissions.push(element.code)
          })
          permissions = permissions.concat(getAllMenuPath(menus))
          commit('SET_MENUS', menus)
          commit('SET_ELEMENTS', elements)
          commit('SET_PERMISSIONS', permissions)
          resolve(data)
        }).catch(error => {
          reject(error)
        })
      })
    },
    LogOut ({commit}) {
      return new Promise(resolve => {
        // 重置vuex全部状态
        commit('SET_TOKEN', undefined)
        // commit('SET_MENUS', [])
        commit('SET_ELEMENTS', [])
        commit('SET_PERMISSIONS', [])
        removeToken()
        resolve()
      })
    }
  }
}

const getAllMenuPath = menus => {
  let paths = []
  menus.forEach(menu => {
    if (menu.children) {
      paths = paths.concat(getAllMenuPath(menu.children))
    } else {
      paths.push(menu.path)
    }
  })
  return paths
}

export default user



